import Vue from 'vue'
import Vuex from 'vuex'
import axios from '@/api/axios-config'
import createPersistedState from 'vuex-persistedstate'
import router from '@/router'
Vue.use(Vuex)


export default new Vuex.Store({
  state: {
    token: '',
    statistics: []
  },
  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
      console.log(state.token,  'state.token in mutations')
    },
    SET_STATISTICS: (state, stat) => state.statistics = stat,
  },
  actions: {
    async getStat({ commit }) {
      try {
        const res = await axios.get('/statistics')
        commit('SET_STATISTICS', res.data)
      } catch (e) {
        console.log(e)
      }
    },
    async logIn({ commit, rootState }, userData) {
      try {
        const res = await axios.post(`auth/login`, userData)
        commit('SET_TOKEN', res.data.token)
        await router.push({ name: 'UserView' })
      } catch (e) {
        console.log(e)
        return 'error'
      }
    },
    async reg({ commit, rootState }, userData) {
      try {
        const res = await axios.post(`auth/register`, userData)
        commit('SET_TOKEN', res.data.token)
        await router.push({ name: 'UserView' })
      } catch (e) {
        console.log(e)
        return 'error'
      }
    },
    async logout({ commit }) {
      try {
        await axios.post(`auth/logout`)
        commit('SET_TOKEN', '')
        await router.push({ name: 'auth' })
      } catch (e) {
        console.log(e)
      }
    },
    async updateCard({ commit, rootState }, data) {
      const cardData = {
        name: data.name,
        purchases: +data.purchases,
        notes: data.notes
      }
      try {
        await axios.patch(`statistics/${data.id}`, cardData)
      } catch (e) {
        console.log(e)
      }
    },
    async createCard({ commit, rootState }, data) {
      const cardData = {
        name: data.name,
        notes: data.notes
      }
      try {
        await axios.post(`statistics`, cardData)
      } catch (e) {
        console.log(e)
      }
    },
    async deleteCard({ commit, rootState }, id) {
      try {
        await axios.delete(`statistics/${id}`)
      } catch (e) {
        console.log(e)
      }
    },

  },
  plugins: [
    createPersistedState({
      key: 'nuxgame',
    }),
  ],
})
